import Grid from "@mui/material/Grid";
import React from "react";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import { PRI_400 } from "../../theme/colors";
import { connect } from "react-redux";
import makeStyles from "@mui/styles/makeStyles";
import { useNavigate, useParams } from "react-router-dom";
import { getHistoryAppDetail } from "../../redux/actions";
import TopNav from "../../components/TopNav";
import { Button } from "@mui/material";
import ModalNotification from "../../components/ModalNotification";
import succImg from "../../assets/hand_succ.png";

const useStyles = makeStyles({
  img: {
    width: "50px",
    height: "50px",
    objectFit: "contain",
    borderRadius: "50%",
  },
  container: {
    paddingTop: "32px",
  },
  navContainer: {
    paddingBottom: "64px",
  },
  dateContainer: {
    borderRadius: "8px !important",
    color: "#FFF !important",
    backgroundColor: PRI_400,
    padding: "8px",
  },
  cancelStamp: {
    position: "absolute",
    padding: "8px",
    border: "3px solid red",
    backgroundColor: "#FFF",
    opacity: "0.8",
    color: "red",
    fontSize: "16px",
    width: "50vw",
    fontWeight: 700,
    textAlign: "center",
  },
  qrWrapper: {
    position: "relative",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
});
const AppointmentDetailPage = (props) => {
  const { stateBahasa, stateHistoryDetail, actGetHistoryAppDetail } = props;
  const { id } = useParams();
  const classes = useStyles();
  const [openSucc, setOpenSucc] = React.useState(true);
  React.useEffect(() => {
    actGetHistoryAppDetail({
      rkun_id: id,
    });
  }, []);
 
  const navigate = useNavigate();
  return (
    <>
      <ModalNotification
        open={openSucc}
        setOpen={setOpenSucc}
        title={
          stateBahasa === "indonesia"
            ? "Berhasil Daftar"
            : "Registration Succesfull"
        }
        subtitle={""}
        img={succImg}
      />
      <Container className={classes.container}>
        <Box className={classes.navContainer}>
          <TopNav
            type="back"
            title={
              stateBahasa === "indonesia"
                ? "Kembali ke halaman utama"
                : "Back to home"
            }
            click={() =>
              navigate("/home", {
                replace: true,
              })
            }
          />
        </Box>
        <Grid container spacing={2}>
          <Grid
            item
            xs={12}
            container
            direction="column"
            justifyContent="center"
            alignItems="center"
          >
            <Typography variant="h3" align="center">
              RSUD CENGKARENG
            </Typography>
            <Typography variant="body2" align="center">
              JL. Bumi Cengkareng Indah No.1, Cengkareng Tim., Kecamatan
              Cengkareng, Kota Jakarta Barat, Daerah Khusus Ibukota Jakarta
              11730
            </Typography>
            <Typography
              variant="body2"
              align="center"
              color="blue"
              onClick={() =>
                window.open("https://goo.gl/maps/n1MF6RVaRYdUfR5q6", "_blank")
              }
            >
              Google Maps
            </Typography>
          </Grid>
          <Grid item xs={12} container direction="column">
            <Typography variant="subtitle1" align="center">
              {`${stateHistoryDetail.is_bpjs ? "BPJS" : "NON-BPJS"} / ${
                stateHistoryDetail.is_lansia ? "LANSIA" : "NON-LANSIA"
              }`}
            </Typography>
          </Grid>
          <Grid item xs={12} container justifyContent="center">
            <div className={classes.qrWrapper}>
              {/* {cancelled && <Box className={classes.cancelStamp}>CANCELED</Box>}
              <QRCode
                value={
                  stateHistoryDetail.rkunid
                    ? stateHistoryDetail.rkunid.toString()
                    : "null"
                }
                size={128}
              /> */}
            </div>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="body1">
              {stateBahasa === "indonesia"
                ? "Nomor antrian anda:"
                : "Your queue:"}
            </Typography>
            <Box
              sx={{
                padding: "3px",
                borderRadius: "3px",
                border: "2px solid " + PRI_400,
              }}
            >
              <Typography variant="h3" align="center">
                {stateHistoryDetail.noantri}
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="body1">
              {stateBahasa === "indonesia"
                ? "Estimasi waktu tindakan:"
                : "Estimated time:"}
            </Typography>
            <Box
              sx={{
                padding: "3px",
                borderRadius: "3px",
                border: "2px solid " + PRI_400,
              }}
            >
              <Typography variant="subtitle1" align="center">
                {stateHistoryDetail.pesanfoot1}
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="body1" align="center">
              {stateBahasa === "indonesia"
                ? "SILAHKAN MENUNGGU NOMOR ANTRIAN DIPANGGIL, MOHON DATANG SETENGAH JAM SEBELUM ESTIMASI"
                : "PLEASE WAIT FOR YOUR TURN, AND COME HALF HOUR EARLIER"}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant="subtitle2" align="center">
              {stateBahasa === "indonesia"
                ? "TERIMAKASIH ATAS KEPERCAYAAN ANDA"
                : "THANK YOU FOR YOUR TRUST"}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Button
              onClick={() =>
                navigate("/home", {
                  replace: true,
                })
              }
              variant="contained"
            >
              {stateBahasa === "indonesia" ? "Lihat history" : "History"}
            </Button>
          </Grid>
        </Grid>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateBahasa: state.app.bahasa,
    stateHistoryDetail: state.register.historyAppDetail,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetHistoryAppDetail: (body, action) =>
      dispatch(getHistoryAppDetail(body, action)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppointmentDetailPage);
